.cropper {
    @include position(relative);
    @include padding(10px);
    background: #ddd;
    height: 450px;
    overflow: hidden;
}

.cropper__container {
    @include position(absolute, 0px);
    @include margin(auto);
    cursor: move;
    >img{
        @include display(block);
    }
}

.cropper__handle {
    @include position(absolute);
    @include display(block);
    @include size(10px);
    background: rgba(222,60,80,.9);
    z-index: 999;
}

.cropper__handle--nw {
    @include position(absolute, -5px null null -5px);
    cursor: nw-resize;
}

.cropper__handle--sw {
    @include position(absolute, null null -5px -5px);
    cursor: sw-resize;
}

.cropper__handle--ne {
    @include position(absolute, -5px -5px null null);
    cursor: ne-resize;
}

.cropper__handle--se {
    @include position(absolute, null -5px -5px null);
    cursor: se-resize;
}

.cropper__overlay {
    @include position(absolute, 50% null null 50%);
    @include box-sizing(content-box);
    z-index: 999;
    border: dashed 2px rgba(222,60,80,.9);
    pointer-events: none;
}